// Width classes
.w-screen {
  width: 100vw;
}


// Create Width Classes
@each $key, $value in $bk-breakpoints {
  $from: map-get($value, from);
  $cols: 24;

  @if $key == 'min' {
    @media screen and (min-width: #{$from}) {
      // Col Classes
      @for $i from 1 through $cols {
        // Col
        .w-#{$i}\/#{$cols} {
          @include make-col($i, $cols);
        }
      }
    }
  } @else if $key != 'min' {
    // Width Segments
    @media screen and (min-width: #{$from}) {
      // Col Classes
      @for $i from 1 through $cols {
        // Col
        .#{$key}\:w-#{$i}\/#{$cols} {
          @include make-col($i, $cols);
        }
      }
    }

    // Screen Class
    @media screen and (min-width: #{$from}) {
      @for $i from 1 through $cols {
        // Col
        .#{$key}\:w-screen {
          width: 100vw;
        }
      }
    }
  }
}


